'use client';

import { useState, useRef, useEffect } from 'react';
import { useSearchParams } from 'next/navigation';
import { Send, Loader2, FileText, ArrowRight } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Textarea } from '@/components/ui/textarea';
import { Card, CardContent } from '@/components/ui/card';
import { ScrollArea } from '@/components/ui/scroll-area';
import { fetchAgentResponse } from '@/lib/streamingClient';
import { MessageBubble } from '@/components/MessageBubble.jsx';
import { FileUploadBox } from '@/components/FileUploadBox.jsx';
import { ThinkingIndicator } from '@/components/ThinkingIndicator.jsx';
import { useToast } from '@/components/ui/use-toast.js';
import { cn } from '@/lib/utils';
import { ThemeToggle } from '@/components/ThemeToggle.jsx';

export default function ChatPage() {
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [file, setFile] = useState(null);
  const scrollRef = useRef(null);
  const { toast } = useToast();

  const handleSendMessage = async (e) => {
    e.preventDefault();

    if ((!input.trim() && !file) || isLoading) return;

    const userMessage = {
      id: Date.now(),
      sender: 'user',
      content: input.trim(),
      file: file ? { name: file.name, type: file.type } : null,
    };

    setMessages((prev) => [...prev, userMessage]);
    setInput('');
    setIsLoading(true);

    const formData = new FormData();
    formData.append('message', input.trim() || `Analyze the uploaded file: ${file.name}`);
    if (file) {
      formData.append('file', file);
      formData.append('fileType', file.type);
      formData.append('fileName', file.name);
    }
    
    // Clear file state after preparing formData
    setFile(null); 

    try {
      // Add a placeholder message for the streaming response
      const agentMessageId = Date.now() + 1;
      const placeholder = {
        id: agentMessageId,
        sender: 'agent',
        content: '',
        status: 'streaming',
        metadata: {},
      };

      setMessages((prev) => [...prev, placeholder]);

      await fetchAgentResponse(formData, (chunk) => {
        setMessages((prev) => {
          const newMessages = [...prev];
          const currentMessageIndex = newMessages.findIndex((m) => m.id === agentMessageId);

          if (currentMessageIndex !== -1) {
            const currentMessage = newMessages[currentMessageIndex];
            
            // Handle different chunk types from the backend
            if (chunk.type === 'text') {
              currentMessage.content += chunk.data;
            } else if (chunk.type === 'thought') {
              // Update the latest thought or initialize
              const thoughtText = chunk.data;
              currentMessage.metadata.thought = thoughtText; 
            } else if (chunk.type === 'chart' || chunk.type === 'email_preview' || chunk.type === 'table') {
              // Store final structured data
              currentMessage.metadata.widget = { type: chunk.type, data: chunk.data };
            } else if (chunk.type === 'error') {
              currentMessage.content += `\n\n**ERROR:** ${chunk.data}`;
            } else if (chunk.type === 'end' || chunk.type === 'final_output') {
               currentMessage.status = 'complete';
               delete currentMessage.metadata.thought;
            }
          }
          return newMessages;
        });
      });

    } catch (error) {
      console.error("Streaming error:", error);
      toast({
        title: "Agent Error",
        description: error.message,
        variant: "destructive",
      });
      setMessages((prev) => 
        prev.map(m => m.id === Date.now() + 1 ? { ...m, content: (m.content || "") + `\n\n**Fatal Error:** ${error.message}`, status: 'complete' } : m)
      );
    } finally {
      setIsLoading(false);
    }
  };

  // Auto-scroll to bottom on new message or update
  useEffect(() => {
    if (scrollRef.current) {
      scrollRef.current.scrollTo({
        top: scrollRef.current.scrollHeight,
        behavior: 'smooth',
      });
    }
  }, [messages]);

  return (
    <div className="flex flex-col h-screen max-w-4xl mx-auto p-4 md:p-6">
      <header className="flex justify-between items-center pb-4 border-b">
        <h1 className="text-2xl font-bold flex items-center gap-2">
          <span className="text-primary">Synapse</span> Agent ðŸ§ 
        </h1>
        <ThemeToggle />
      </header>

      <ScrollArea className="flex-1 my-4 p-4 rounded-xl border bg-muted/20" ref={scrollRef}>
        <div className="flex flex-col space-y-4">
          {messages.length === 0 && (
            <div className="flex flex-col items-center justify-center h-[60vh] text-center text-muted-foreground">
                <FileText className="w-12 h-12 mb-4 text-primary" />
                <h2 className="text-lg font-semibold">Welcome to Synapse</h2>
                <p className="max-w-xs">Upload a CSV, PDF, or image and ask me to analyze, generate, or verify claims.</p>
                <div className="mt-4">
                  <FileUploadBox setFile={setFile} />
                </div>
            </div>
          )}
          {messages.map((msg) => (
            <MessageBubble key={msg.id} message={msg} setMessages={setMessages} />
          ))}
          {isLoading && <ThinkingIndicator text={messages.slice(-1)[0]?.metadata?.thought || "Thinking..."} />}
        </div>
      </ScrollArea>

      <div className="mt-auto pt-4">
        <FileUploadBox file={file} setFile={setFile} disabled={isLoading} />
        
        <form onSubmit={handleSendMessage} className="flex items-end gap-2 mt-2">
          <Textarea
            value={input}
            onChange={(e) => setInput(e.target.value)}
            placeholder="Ask me to analyze your file, draft a letter, or verify claims..."
            disabled={isLoading}
            rows={2}
            className="flex-1 resize-none pr-10"
            onKeyDown={(e) => {
              if (e.key === 'Enter' && !e.shiftKey) {
                e.preventDefault();
                handleSendMessage(e);
              }
            }}
          />
          <Button type="submit" size="icon" disabled={isLoading || (!input.trim() && !file)}>
            {isLoading ? (
              <Loader2 className="h-4 w-4 animate-spin" />
            ) : (
              <Send className="h-4 w-4" />
            )}
          </Button>
        </form>
      </div>
    </div>
  );
}